
.appLogo{
    width: 150px;
    height: 150px;
}
  
  @media (prefers-reduced-motion: no-preference) {
    .appLogoN {
      animation: applogospin infinite 5s linear;
    }
  }
  
  
  
  @keyframes applogospin {
    from {
      opacity: 0;
    }
    to {
      opacity: 100;
    }
  }
  